.main-container{
    

    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin:0 auto;
    min-width: 280px;

    background-color:$background-main-container;
          

        @media screen and (min-width: 768px)  {
            margin-left: -30px;
            margin-top: -30px;       
                    }

    
   

}

.card {
    display: block;
    list-style: none;
    flex-basis: calc(100% / 1 - 30px);

    @media screen and (max-width: 767px){
              &:not(:last-child) {
            margin-bottom: 20px;
          }
    }
   

@media screen and (min-width: 768px) {
    flex-basis: calc(100% / 2 - 30px);
    margin-left: 30px;
    margin-top: 30px;
}

@media screen and (min-width: 1024px){
    flex-basis: calc(100% / 3 - 30px);
    margin-left: 30px;
    margin-top: 30px;
}
}


